import React from 'react';
import EstruturaPagina from '../../componentes/EstruturaPagina';
import './estilo.css';

const Missao = () => {
    return(
        <EstruturaPagina>
                <div className="missao-container">
                    <div className="missao">
                    <h2 className="titulo-sessao">Qual a nossa <b>Missão?</b></h2>
                    <p className="descricao">Prezamos pela saúde bucal e o bem estar de nossos clientes, estamos no mercado há 20 anos proporcionando sorrisos aos nossos clientes!</p>

                    <img className="sorriso" src="assets/sorriso.jpg" alt="teste"/>
                    </div>
                </div>
        </EstruturaPagina>
    );
}

export default Missao;