import React from 'react';
import EstruturaPagina from '../../componentes/EstruturaPagina';
import './estilo.css';

const Agenda = () => {
    return(
        <EstruturaPagina>
            
            <div className="agenda-container">
            <div className="Agenda">
                <h2 className="titulo-sessao">Agenda de <b>Atendimento</b></h2>
                
                <ul className="funcionamento">
                    
                    <li>
                        <img className="img-agendamento" src="assets/mulher1.jpeg" alt="primeiro agendamento" title="primeiro agendamento" />
                        <p><b>Carol Danvers</b></p>
                        <p>Status de Agendamento: <b className="confirm">Confirmado</b></p>
                        <p>Horário: <b>11:30</b></p>
                        <p>Dia: <b>23/03</b></p>
                    </li>

                    <li>
                        <img className="img-agendamento" src="assets/mulher2.jpg" alt="segundo agendamento" title="segundo agendamento" />
                        <p><b>Mary Jane</b></p>
                        <p>Status de Agendamento: <b className="confirm">Confirmado</b></p>
                        <p>Horário: <b>12:00</b></p>
                        <p>Dia: <b>23/03</b></p>
                    </li>

                    <li>
                        <img className="img-agendamento" src="assets/mulher3.jpg" alt="segundo agendamento" title="segundo agendamento" />
                        <p><b>Scarlett Johansson</b></p>
                        <p>Status de Agendamento: <b className="confirm">Confirmado</b></p>
                        <p>Horário: <b>12:30</b></p>
                        <p>Dia: <b>23/03</b></p>
                    </li>

                </ul>

                <br/>
                <h2 className="titulo-sessao">Quer agendar uma consulta? Agende aqui em baixo!</h2>
                <br/>
                <form className="agendarconsulta">
                    <label>
                        <p>Nome completo:</p>
                        <input type="text" name="name"/>
                    </label>
                    <label>
                        <p>E-mail:</p>
                        <input type="text" name="name"/>
                    </label>
                    <label>
                        <p>Data de Nascimento:</p>
                        <input type="date" name="nascimento"/>
                    </label>
                    <label>
                        <p>Endereço:</p>
                        <input type="text" name="name"/>
                    </label>
                    <label>
                        <p>RG:</p>
                        <input type="text" name="name"/>
                    </label>
                    <label>
                        <p>CEP:</p>
                        <input type="text" name="name"/>
                    </label>
                    <label>
                        <p>Telefone:</p>
                        <input type="text" name="name"/>
                    </label>
                    
                </form>
            </div>
            </div>
            <br></br>
            <br></br>
            <br></br>
            
        </EstruturaPagina>
    );
}

export default Agenda;