import React from 'react';
import EstruturaPagina from '../../componentes/EstruturaPagina';
import './estilo.css';

const Convenios = () => {
    return(
        <EstruturaPagina>
            <div className="convenios-container">
                <div className="Convenios">
                    <h2 className="titulo-sessao">Lista de <b>Convênios</b></h2>

                    <div className="firstSquare">
                        <strong>Convênio Clássico</strong>
                        <div className="firstPlano">Selecione a cobertura:</div>
                        <br />
                        <select id="pfClassic" class="select1" name="PlanoClassico">
                            <option value="200Doc" selected>Nacional (Dental 205) </option>
                            <option value="50" selected>Regional (Regional 50 NE) </option>
                        </select>
                        
                        <div id="firstPessoas" class="firstPessoas"><b>Pessoas:</b></div>
                        <select id="pfClassic" class="select2" name="PlanoClassico">
                                <option value="1a3" selected>1 a 3 pessoas </option>
                                <option value="3mais" selected>3 pessoas ou + </option>
                        </select>

                        <div id="firstApartir" class="firstApartir"><b>A partir de:</b></div>
                        <div class="firstCifra">R$</div>
                        <div id="firstValorRedondo" class="firstValorRedondo">27</div>
                        <div id="firstCent" class="firtsCent">,20/mês</div>
                        <br/>
                        <div id="firstBtn" class="btnC"><a class="alink" href="teste">COMPRAR</a></div>
                    </div>
                        <br /> <br/>

                    <div className="SecondSquare">
                        <strong>Convênio Estético</strong>
                        <div className="secondPlano">Selecione a cobertura:</div>
                        <br />
                        <select id="pfClassic" class="select1" name="PlanoClassico">
                            <option value="E35Dental" selected>Prótese (Dental E35) </option>
                            <option value="E45Dental" selected>Orto (Dental E45) </option>
                        </select>
                        
                        <div id="firstPessoas" class="firstPessoas"><b>Pessoas:</b></div>
                        <select id="pfClassic" class="select2" name="PlanoClassico">
                                <option value="plano2_Unico" selected>1 pessoa</option>
                                
                        </select>

                        <div id="firstApartir" class="firstApartir"><b>A partir de:</b></div>
                        <div class="firstCifra">R$</div>
                        <div id="firstValorRedondo" class="firstValorRedondo">92</div>
                        <div id="firstCent" class="firtsCent">,00/mês</div>
                        <br/>
                        <div id="firstBtn" class="btnC"><a class="alink" href="teste">COMPRAR</a></div>
                    </div>
                        <br /> <br/>

                    <div className="ThirdSquare">
                        <strong>Convênio Kids</strong>
                        <div className="secondPlano">Selecione a cobertura:</div>
                        <br />
                        <select id="pfClassic" class="select1" name="PlanoClassico">
                            <option value="E35Dental" selected>Kids (Até 14 anos)</option>
                            
                        </select>
                        
                        <div id="firstPessoas" class="firstPessoas"><b>Pessoas:</b></div>
                        <select id="pfClassic" class="select2" name="PlanoClassico">
                                <option value="plano2_Unico" selected>1 pessoa</option>
                                
                        </select>

                        <div id="firstApartir" class="firstApartir"><b>A partir de:</b></div>
                        <div class="firstCifra">R$</div>
                        <div id="firstValorRedondo" class="firstValorRedondo">29</div>
                        <div id="firstCent" class="firtsCent">,90/mês</div>
                        <br/>
                        <div id="firstBtn" class="btnC"><a class="alink" href="teste">COMPRAR</a></div>
                    </div>
                        <br /> <br/>
                    

                    

                    
                   
                </div>
            </div>
        </EstruturaPagina>
    );
}

export default Convenios;