import React from 'react';
import EstruturaPagina from '../../componentes/EstruturaPagina';
import Mapa from '../../componentes/Mapa';
import './estilo.css';

const Contato = () => {
    return(
        <EstruturaPagina>
            <div className="contato-container">
                <div className="contato-mapa">
                    <h2 className="titulo-sessao">Horários de <b>Atendimento</b></h2>
                    <p>Agende uma consulta pelo telefone: (11) 58423-7000</p>

                    <ul className="lista-medicos">
                        <li>
                            <img className="img-aparelho" src="assets/medico01.png" alt="Foto do Doutor Hélio" title="Foto do Doutor Hélio" />
                            <p>Dr. Hélio</p>
                            <p>Segundas, quartas e sextas das 09:00 às 16:00</p>
                        </li>
                        <li>
                            <img className="img-aparelho" src="assets/medico02.png" alt="Foto do Doutor Carlos" title="Foto do Doutor Carlos" />
                            <p>Dr. Carlos</p>
                            <p>Terças e quintas das 13:00 às 18:00</p>
                        </li>
                        <li>
                            <img className="img-aparelho" src="assets/medico03.png" alt="Foto da Doutora Suzana" title="Foto da Doutora Suzana" />
                            <p>Dra. Suzana </p>
                            <p>Terças, quintas e sábados das 08:00 às 12:00</p>
                        </li>
                    </ul>
                </div>

                <div className="contato-mapa">
                    <h2 className="titulo-sessao">Onde estamos <b>localizados</b>?</h2>
                    <p>R. Bento Branco de Andrade Filho, 379 - São Paulo - SP. CEP: 04757-000</p>
                    <Mapa />
                </div>

            </div>
        </EstruturaPagina>
            
        
     );
}

export default Contato;